/***/ (function(module, exports) {

	module.exports = {"data":{"wordpressPost":{"title":"The Top Three Things Confident Women Do Every Day","content":"<p>We&#8217;re lucky in that we have some super cool friends.</p>\n<p>They&#8217;re hilarious and kind and supportive but better still (maybe) they have awesome careers which mean they know stuff. And, because they&#8217;re our friends, they&#8217;re willing to share. One such GF &#8211; <a href=\"http://the-beautyinsider.com/boost-beauty-confidence/\">Laura Moore</a> &#8211; ace life coach why just happens to know how to make you confidence soar.</p>\n<p>Laura, the founder of <a href=\"http://uppy.com.au\">coaching firm Uppy</a>, has helped us to look at our lives with a fresh, new confident perspective. Here are the three tricks that she taught us. Now, you can learn them and boost your confidence too.</p>\n<p>&nbsp;</p>\n<p><strong>Number One &#8211; Identify What&#8217;s Really Important To You</strong><br />\nWe get so lost in the ‘shoulds’ that we forget about what really matters to us, says Laura.</p>\n<p>This can lead to us trying to achieve the unachievable and beating ourselves up when we ‘fail’ to get there.</p>\n<p>Laura says that by bringing it all back what truly matters to you, the pressure valve will be released and the ‘shoulds’ can all wash over you like a wave &#8211; because you know who you are, what you stand for and where you’re heading. Once you can stand strong in your own space you will have the greatest confidence of all.</p>\n<p>To get there she recommends you do the following:<br />\n• Discover or re-discover your core values<br />\n• Have clearly defined goals for all areas of your life<br />\n• Ensure you are serving these every single day</p>\n<figure id=\"attachment_2301\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2301\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04150640/Smile-JPP-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04150640/Smile-JPP-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04150640/Smile-JPP-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04150640/Smile-JPP.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@blakelively</figcaption></figure>\n<p><em>Blake Lively is one of our favourites.Why because she always keeps it real, keeps it honest and strives only to be her best self &#8211; nobody else&#8217;s. Kudos.</em></p>\n<p><strong>Number Two &#8211; Never Compare or Judge</strong><br />\nOne of the greatest challenges in life is to let go of comparing yourself to others, says Laura.</p>\n<p>&#8220;Remember that we have no idea what is going on behind closed doors and I’m fairly certain you can guarantee they will have a lot of the same stuff going on as you,&#8221; says Laura.</p>\n<p>&#8220;They perhaps just deal with it or mask it better. It’s ok to strive to be like someone you admire, but when doing this keep your attention on the attributes you wish to emulate and look at it as an opportunity to grow. When you find your inner chatter telling yourself that you don’t have what they have, simply add the word ‘yet’ onto the sentence: “I don’t have what they have yet” – this will switch your brain out of ‘beat-up’ mode and into problem-solving mode so you can figure out how to get there and move forward.</p>\n<img class=\"alignnone size-medium wp-image-2305\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152751/JPG-235x300.jpg\" alt=\"\" width=\"235\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152751/JPG-235x300.jpg 235w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152751/JPG.jpg 346w\" sizes=\"(max-width: 235px) 100vw, 235px\" /><img class=\"alignnone size-medium wp-image-2306\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152808/goals-JPG-300x221.jpg\" alt=\"\" width=\"300\" height=\"221\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152808/goals-JPG-300x221.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152808/goals-JPG.jpg 499w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>Research that goals are more likely to be achieved when they&#8217;re noted down. A beautiful notebook will that make task a pleasure. For this one and more visit <a href=\"http://www.kikki-k.com/notebooks-journals/inspiration-journal\">Kikki-k.com</a></p>\n<p><strong>Number Three &#8211; Celebrate Your Success </strong><br />\nAlthough you are indeed a strong independent woman (or man), your subconscious brain is still kind of child-like, explains Laura. You need to speak to it nicely and tell it when it’s doing a good job, then it is much more likely to play nice and do more of the good stuff. This is classic positive reinforcement. If you consistently speak badly to it and make it feel like a failure, the chances are it will act up and become defiant, she says.</p>\n<p>&#8220;So start rewarding yourself and celebrating every time something goes well – I guarantee it will make you feel great and motivate you to keep moving forward to do even more next time… and then confidence is inevitable,&#8221; she adds.</p>\n<p>This doesn’t have to mean you bring out the champagne every time you make a sale or have a great conversation with a team member &#8211; simply make a point of telling your inner child it’s doing good.</p>\n<p>So in summary, the key thing that confident women do daily is actually make it all about them. Rather than getting wrapped up in what everyone else is doing and what they think they ‘should’ be doing, they stay true to themselves and what they want. It really is that simple.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/04152029/profile-JPG.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"f7646e66-f9a1-5627-b0b7-25126530a01a"}}

/***/ })

